@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,100;0,400;0,700;1,100;1,600&display=swap');

* {
  --black: #0e0d0e;
  --gray: #19181a;
  --red: #963c3d;
}


.Page {
  text-align: center;
  color: white;
  font-family: 'IBM Plex Mono', monospace;
}

.Page-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .Page-logo {
    animation: Page-logo-spin infinite 20s linear;
  }
}

.Page-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Page-link {
  color: #61dafb;
}

@keyframes Page-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
